body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue'
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#body-container-wrapper {
  display: flex;
  justify-content: space-evenly;
}

#body-container {
  margin-top: 50px;
  width: 1300px;
}

h1 {
  font-family: 'SF Pro Rounded';
  font-weight: bold;
  font-size: 30px;
}

h2 {
  font-family: 'SF Pro Rounded';
  font-weight: bold;
  font-size: 24px;
}

button {
  font-family: 'SF Pro Rounded';
  font-weight: bold;
  border-radius: 12px;
  font-size: 14px;
  padding: 10px 28px;
  border: 0;
}

.button-greater {
  font-size: 20px;
}

.mint-color {
  background-color: #A2E3C4;
}

.light-color {
  background-color: #F0F7F4;
}

.dark-color {
  background-color: #3C493F;
}

.blank-button {
  background-color: white;
  border: 2px solid black;
}

body {
  font-family: 'Roboto';
  font-size: 14px;
  background-color: #A2E3C4;
}

.menu-cell {
  background-color: white;
  border-radius: 12px;
  padding: 14px;
}

.upper-aux-cell {
  width: 223px;
  height: 275px;
}

#main-chat-window {
  height: 650px;
  width: 550px;
}

.dialogue-bubble {
  background-color: #F0F7F4;
}

#panel-container {
  height: 680px;
  width: 1300px;
}