#demo-window {
    width: 750px;
    min-height: 300px;
}

.large-cell {
    border: 2px solid black;
    padding: 10px;
    background-color: gray;

    min-width: 100px;
    min-height: 50px;
    height: 100%;
    margin-top: 100px;
}

.menu-input-holder {
    background-color: lightgray;
    font-size: 12px;
}

.menu-input {
    font-size: 16px;
}

.chat-window {
    background-color: whitesmoke;
}

.dialogue-window {
    height: 200px;
    overflow-y: scroll;
    overflow-x: hidden;
}

h2 {
    font-size: 18px;
}

textarea, input, button {
    border: 0;
}

input {
    padding-left: 10px;
    font-size: 14px;
}

textarea {
    font-family: Roboto;
    padding: 10px;
    overflow-y: scroll;
    resize: none;
    font-size: 14px;
}

textarea :focus {
    background-color: black;
    transition-duration: 1s;
}

select {
    border: 0;
    padding-left: 5px;
}

.blank-button {
    border: 1px solid black;
    background-color: white;
}

.black-square {
    background-color: black;
    width: 100px;
    height: 100px;
    z-index: 100;
}

.chat-bubble-container {
    width: 550px;
    margin-bottom: 10px;
}

.chat-bubble {
    max-width: 480px;
    width: auto;
    display: inline-block;
    background: rgba(240,247,244,1);
    border-radius: 12px;
    padding: 14px;
    font-size: 18px;
    margin-bottom: 5px;
    text-align: left;
}

.narrative-block {
    color: rgba(60,73,63,1);
    font-family: "SF Pro Rounded";
    font-weight: Bold;
    font-size: 18px;
    border-radius: 12px;
    padding: 14px;
    margin-bottom: 5px;
}

.bubble-annot {
    color: rgba(60,73,63,1);
    font-family: "SF Pro Rounded";
    font-weight: Bolder;
    font-size: 16px;
}

.left-float {
    float: left;
    text-align: left;
}

.right-float {
    float: right;
    text-align: right
}

.dialogue-container {
    height: 560px;
    overflow: scroll;
}

.hidden-anchor {
    height: 10px;
    width: 600px;
    display: hidden;
}

button:focus {
    outline: thin dotted;
}